<template>
  <div id="app">
    <pr-row class="navigation_row">
      <pr-col span="4">
        <div class="navigation_title">
          <span>gnoz</span>Validator
        </div>
      </pr-col>
      <pr-col span="20">
        <ul class="navigation_list">
          <router-link tag="li"
            to="/"
            exact>首页</router-link>
          <router-link tag="li"
            to="/doc">文档</router-link>
        </ul>
      </pr-col>
    </pr-row>
    <router-view />
  </div>
</template>

<script>
import './assets/style/reset.less'
import './assets/style/common.less'
export default {
  name: 'App'
}
</script>

<style lang="less">
#app {
  min-width: 1200px;
}
.navigation {
  &_row {
    position: fixed;
    left: 0;
    z-index: 99;
    width: 100%;
    background: #fff;

    .pr-col-20 {
      margin-bottom: 0;
    }
  }

  &_title {
    padding: 15px 0;
    text-align: center;
    color: coral;
  }

  &_list {
    li {
      display: inline-block;
      padding: 15px;

      &:hover {
        cursor: pointer;
      }
    }
  }
}
</style>



